// https://observablehq.com/@corpetty/test-graph-embed@178
import define1 from "../@jashkenas/inputs.js";

export default function define(runtime, observer) {
  const main = runtime.module();
  main.variable(observer()).define(["md"], function(md){return(
md`# Status Token Economics: ENS Usernames`
)});
  main.variable(observer("viewof numberStatusUsers")).define("viewof numberStatusUsers", ["slider"], function(slider){return(
slider({
  min: 1000, 
  max: 1000000000, 
  step: 1000, 
  value: 1000, 
  format: ",",
  title: "Number of current Status users", 
  description: "Increments in 1000 users"
})
)});
  main.variable(observer("numberStatusUsers")).define("numberStatusUsers", ["Generators", "viewof numberStatusUsers"], (G, _) => G.input(_));
  main.variable(observer("viewof userGrowthRate")).define("viewof userGrowthRate", ["slider"], function(slider){return(
slider({
  min: .10, 
  max: 10, 
  step: .10, 
  value: 3, 
  title: "User growth rate per annum (percent)", 
  description: "percentages from 10% through 1000%"
})
)});
  main.variable(observer("userGrowthRate")).define("userGrowthRate", ["Generators", "viewof userGrowthRate"], (G, _) => G.input(_));
  main.variable(observer("viewof userChurnRate")).define("viewof userChurnRate", ["slider"], function(slider){return(
slider({
  min: .01, 
  max: 1, 
  step: .01, 
  value: .10, 
  title: "User churn rate per annum (percent)", 
  description: "percentages from 1% through 100%"
})
)});
  main.variable(observer("userChurnRate")).define("userChurnRate", ["Generators", "viewof userChurnRate"], (G, _) => G.input(_));
  main.variable(observer("users")).define("users", ["numberStatusUsers","userGrowthRate","userChurnRate"], function(numberStatusUsers,userGrowthRate,userChurnRate)
{
  let usersArr = [numberStatusUsers];
  for(let i=1; i < 10; i++) {
    let newUsers = usersArr[i-1] * userGrowthRate;
    let lostUsers = usersArr[i-1] * userChurnRate;
    usersArr[i] = Math.round(usersArr[i-1] + (newUsers - lostUsers));
  }
  return usersArr;
}
);
  main.variable(observer("usersBarData")).define("usersBarData", ["users"], function(users)
{
  return users.map((users, idx) => ({ name: `year ${idx+1}`, year: idx+1, users, value: users, tag: "users"}))
}
);
  main.variable(observer("viewof userbar")).define("viewof userbar", ["vegalite","usersBarData","width"], function(vegalite,usersBarData,width){return(
vegalite({
  data: { values: usersBarData },
  mark: {
    type: "bar",
  },
  autosize: "fit",
  width: width,
  encoding: {
    x: {
      field: "year",
      type: "quantitative"
    },
    y: {
      field: "users",
      type: "quantitative"
    },
    color: {
      field: "tag",
      legend: {
        orient: "top-left",
        title: null
      },
    },
    tooltip: {
      field: "users", 
      type: "quantitative"
    }
  }
})
)});
  main.variable(observer("userbar")).define("userbar", ["Generators", "viewof userbar"], (G, _) => G.input(_));
  main.variable(observer("viewof percentUsersRegisterName")).define("viewof percentUsersRegisterName", ["slider"], function(slider){return(
slider({
  min: 0, 
  max: 1, 
  step: .01, 
  value: .5, 
  title: "Percentage of users that register a username", 
  description: "Increments in percentages of 1"
})
)});
  main.variable(observer("percentUsersRegisterName")).define("percentUsersRegisterName", ["Generators", "viewof percentUsersRegisterName"], (G, _) => G.input(_));
  main.variable(observer("viewof removalRate")).define("viewof removalRate", ["slider"], function(slider){return(
slider({
  min: 0, 
  max: 1, 
  step: 0.01, 
  value: .1, 
  title: "Percentage of people who release their ENS username", 
  description: "Increments in percentages of 1"
})
)});
  main.variable(observer("removalRate")).define("removalRate", ["Generators", "viewof removalRate"], (G, _) => G.input(_));
  main.variable(observer("ensUsers")).define("ensUsers", ["users","percentUsersRegisterName","removalRate"], function(users,percentUsersRegisterName,removalRate){return(
users.map(users => users * percentUsersRegisterName * removalRate)
)});
  main.variable(observer("ensUsersBarData")).define("ensUsersBarData", ["ensUsers","users"], function(ensUsers,users)
{
  let ensUsersData = ensUsers.map((users, idx) => ({ name: `year ${idx+1}`, year: idx+1, users, value: users, tag: "ens"}))
  let nonEnsUsersData = users.map((users, idx) => ({ name: `year ${idx+1}`, year: idx+1, users: users - ensUsers[idx], value: users - ensUsers[idx], tag: "no ens"}))
  return ensUsersData.concat(nonEnsUsersData)
}
);
  main.variable(observer("viewof ensUserbar")).define("viewof ensUserbar", ["vegalite","ensUsersBarData","width"], function(vegalite,ensUsersBarData,width){return(
vegalite({
  data: { values: ensUsersBarData },
  mark: "bar",
  autosize: "fit",
  width: width,
  encoding: {
    x: {
      field: "year",
      type: "quantitative"
    },
    y: {
      aggregate: "sum",
      field: "users",
      type: "quantitative"
    },
    color: {
      field: "tag",
      type: "nominal",
      legend: {
        title: null,
        orient: "top-left"
      },
      scale: {
        domain: ["no ens", "ens"],
        range: ["#d12525", "#000000"]
      }
    },
    tooltip: {
      field: "users", 
      type: "quantitative"
    }
  }
})
)});
  main.variable(observer("ensUserbar")).define("ensUserbar", ["Generators", "viewof ensUserbar"], (G, _) => G.input(_));
  main.variable(observer("viewof amountDepositSnt")).define("viewof amountDepositSnt", ["slider"], function(slider){return(
slider({
  min: 0, 
  max: 1000, 
  step: 1, 
  value: 10, 
  title: "Amount of SNT required to register ENS username", 
  description: "Increments by 1"
})
)});
  main.variable(observer("amountDepositSnt")).define("amountDepositSnt", ["Generators", "viewof amountDepositSnt"], (G, _) => G.input(_));
  main.variable(observer("viewof sntLockupTime")).define("viewof sntLockupTime", ["slider"], function(slider){return(
slider({
  min: 1, 
  max: 10, 
  step: 1, 
  value: 1, 
  title: "Amount of time SNT is locked up in years", 
  description: "Increments by 1 year"
})
)});
  main.variable(observer("sntLockupTime")).define("sntLockupTime", ["Generators", "viewof sntLockupTime"], (G, _) => G.input(_));
  main.variable(observer("amountSnt")).define("amountSnt", ["ensUsers","amountDepositSnt"], function(ensUsers,amountDepositSnt){return(
ensUsers.map(users => users * amountDepositSnt)
)});
  main.variable(observer("lockedSntBarData")).define("lockedSntBarData", ["amountSnt"], function(amountSnt)
{
  return amountSnt.map((snt, idx) => ({ name: `year ${idx+1}`, year: idx+1, snt, value: snt, tag: "SNT"}))
}
);
  main.variable(observer("viewof lockedSntBar")).define("viewof lockedSntBar", ["vegalite","lockedSntBarData","width"], function(vegalite,lockedSntBarData,width){return(
vegalite({
  data: { values: lockedSntBarData },
  mark: "bar",
  autosize: "fit",
  width: width,
  encoding: {
    x: {
      field: "year",
      type: "quantitative"
    },
    y: {
      aggregate: "sum",
      field: "snt",
      type: "quantitative",
      title: "SNT locked up"
    },
    color: {
      field: "tag",
      type: "nominal",
      legend: {
        title: null,
        orient: "top-left"
      },
      scale: {
        domain: ["SNT"],
        range: ["#4360DF"]
      }
    }
  }
})
)});
  main.variable(observer("lockedSntBar")).define("lockedSntBar", ["Generators", "viewof lockedSntBar"], (G, _) => G.input(_));
  main.variable(observer("viewof sntPrice")).define("viewof sntPrice", ["slider"], function(slider){return(
slider({
  min: 0.01, 
  max: 100, 
  step: 0.01, 
  value: 0.03, 
  title: "Price of SNT in US Dollars", 
  description: "Increments in cents"
})
)});
  main.variable(observer("sntPrice")).define("sntPrice", ["Generators", "viewof sntPrice"], (G, _) => G.input(_));
  main.variable(observer("amountUsd")).define("amountUsd", ["amountSnt","sntPrice"], function(amountSnt,sntPrice){return(
amountSnt.map(snt => snt * sntPrice)
)});
  main.variable(observer("lockedUsdBarData")).define("lockedUsdBarData", ["amountUsd"], function(amountUsd)
{
  return amountUsd.map((usd, idx) => ({ name: `year ${idx+1}`, year: idx+1, usd, value: usd, tag: "USD"}))
}
);
  main.variable(observer("viewof lockedUsdBar")).define("viewof lockedUsdBar", ["vegalite","lockedUsdBarData","width"], function(vegalite,lockedUsdBarData,width){return(
vegalite({
  data: { values: lockedUsdBarData },
  mark: "bar",
  autosize: "fit",
  width: width,
  encoding: {
    x: {
      field: "year",
      type: "quantitative"
    },
    y: {
      aggregate: "sum",
      field: "usd",
      type: "quantitative",
      title: "USD locked up"
    },
    color: {
      field: "tag",
      type: "nominal",
      legend: {
        title: null,
        orient: "top-left"
      },
      scale: {
        domain: ["USD"],
        range: ["#2ba83e"]
      }
    }
  }
})
)});
  main.variable(observer("lockedUsdBar")).define("lockedUsdBar", ["Generators", "viewof lockedUsdBar"], (G, _) => G.input(_));
  main.variable(observer()).define(["md"], function(md){return(
md`## Imports and Utility`
)});
  const child1 = runtime.module(define1);
  main.import("slider", child1);
  main.variable(observer("vegalite")).define("vegalite", ["require"], function(require){return(
require('@observablehq/vega-lite')
)});
  main.variable(observer("width")).define("width", function(){return(
375
)});
  return main;
}
